import React from "react";
import Todo from "./Todo";

export default function TodoList({ todos, setTodos, filterTodos }) {
  return (
    <div className="todo-container">
      <ul className="todo-list">
        {filterTodos.map((todo) => (
          <Todo
            todos={todos}
            setTodos={setTodos}
            key={todo.id}
            todo={todo}
            text={todo.text}
            date={todo.date}
            filterTodos={filterTodos}
          />
        ))}
      </ul>
    </div>
  );
}
